import axios from 'axios';
import lz from '@lizhife/lz-jssdk';

export function wxConfig(url) {
  const {
    protocol, host, pathname, search,
  } = window.location;
  const link = `${protocol}//${host}${pathname}${search}`;

  axios({
    url,
    params: {
      currentURL: link,
    },
  }).then((res) => {
    const { data } = res;
    const { wx } = window;

    if (!data) {
      return;
    }

    wx.config({
      debug: false, // ,apialertpclogpc
      appId: data.appid, // 
      timestamp: data.timestamp, // 
      nonceStr: data.nonceStr, // 
      signature: data.signature, // 1
      jsApiList: [
        'checkJsApi',
        'onMenuShareTimeline',
        'onMenuShareAppMessage',
        'onMenuShareQQ',
        'onMenuShareWeibo',
        'hideMenuItems',
        'showMenuItems',
        'hideAllNonBaseMenuItem',
        'showAllNonBaseMenuItem',
        'translateVoice',
        'startRecord',
        'stopRecord',
        'onRecordEnd',
        'playVoice',
        'pauseVoice',
        'stopVoice',
        'uploadVoice',
        'downloadVoice',
        'chooseImage',
        'previewImage',
        'uploadImage',
        'downloadImage',
        'getNetworkType',
        'openLocation',
        'getLocation',
        'hideOptionMenu',
        'showOptionMenu',
        'closeWindow',
        'scanQRCode',
        'chooseWXPay',
        'openProductSpecificView',
        'addCard',
        'chooseCard',
        'openCard',
      ], // JSJS2
    });
  });
}

export function appConfig(url) {
  lz.config({
    debug: false,
    url,
    apiList: [
      'getToken',
      'getSessionUser',
      'gotoLogin',
      'shareUrl',
      'startRecordVoice',
      'stopRecordVoice',
      'uploadRecordVoice',
      'replayRecordVoice',
      'shareImage',
      'saveImage',
    ],
    eventList: ['user:login', 'recordStateChange'],
  });
}



